html {
  --main-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.nav_item {
  height: 100%;
}

.nav {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav_link {
  color: #000;
  text-transform: uppercase;
  height: 100%;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_list {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.logo_img {
  width: 40px;
  height: 40px;
  display: block;
  left: 50px;
}

.nav_link:hover, .is-active {
  color: var(--main-color);
}

.is-active:after {
  color: #00acdc;
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
}
